
import { NextResponse } from 'next/server';
import clientPromise from '@/lib/mongodb';

export async function GET() {
    try {
        const client = await clientPromise;
        const db = client.db('lumina-database'); // Use the DB name from the URI if possible, or default
        // Just a simple ping
        await db.command({ ping: 1 });
        return NextResponse.json({ status: 'Connected to MongoDB', dbName: db.databaseName });
    } catch (e: any) {
        return NextResponse.json({ status: 'Failed', error: e.message }, { status: 500 });
    }
}
